.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/*this is for border of card*/
.card-deck .card {
  border-color: #a1d884;
}

.btn-primary,
.btn-primary:visited {
  background-color: #a1d884 !important;
  border-color: #a1d884 !important;
}
.btn-primary:hover,
.btn-primary:active {
  background-color: #006845 !important;
  border-color: #006845 !important;
}

/*Button for radio choose*/
.MuiRadio-colorPrimary.Mui-checked {
  color: #a1d884 !important;
}

/*Button for successful message*/
.swal-button {
  background-color: #a1d884 !important;
  border-color: #a1d884 !important;
}
.swal-button:active,
.swal-button:hover {
  background-color: #006845 !important;
  border-color: #006845 !important;
}
/*for dialog box submit button*/
.MuiButton-textPrimary {
  color: #006845 !important;
}

/*Navbar button*/
.navbar-default {
  background-color: #006845;
  border-color: #006845;
}

.testLogoParent {
  margin: auto;
}
.testLogo {
  height: 50px;
  width: 50px;
  margin-top: -30px;
}

/*Navigation bar links*/
.nav-link {
  font-family: "Didot" !important;
  font-size: 21px;
  color: #8d683f !important;
}

.nav-link.active-link {
  font-family: "Didot-Italic" !important;
  color: red !important;

  font-size: 21px;
}

.navbar.bg-dark {
  background-color: #e6d57b !important;
}

.MuiTypography-body1 {
  font-family: "Didot" !important;
}

.img-fluid {
  max-height: 400px;
  display: block;
  margin: auto;
}

.row {
  display: table;
}

[class*="col-"] {
  float: none;
  display: table-cell;
  vertical-align: top;
}

.indented {
  text-indent: 35px;
}

.rsvp-container {
  padding-bottom: 60px;
}

.background-pic-contact {
  background-image: url("./images/Contactanos/blurry.jpg");
  background-size: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow-y: hidden !important;
}

.jumbotron {
  margin: auto !important;
}

.padding-ceremonia {
  padding-bottom: 60px;
}

html,
body {
  max-width: 100%;
  overflow-x: hidden;
  font-family: "Didot" !important;
}
